import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

let store = new Vuex.Store({
    state: {
        employee: [],
        training: JSON.parse(localStorage.getItem('training') || '[]'),
        // category: [],
        category: JSON.parse(localStorage.getItem('category') || '[]'),
        news: JSON.parse(localStorage.getItem('news') || '[]'),
        courses: JSON.parse(localStorage.getItem('courses') || '[]'),
        // product: [],
        product: JSON.parse(localStorage.getItem('product') || '[]'),
        services: JSON.parse(localStorage.getItem('services') || '[]'),
        newservices: []
    },
    mutations: {
        SET_EMPLOYEE: (state, employee) => {
            state.employee = employee;
        },
        SET_CATEGORY: (state, category) => {
            state.category = category
            localStorage.setItem('category', JSON.stringify(state.category))
        },
        SET_NEWS: (state, news) => {
            state.news = news;
            localStorage.setItem('news', JSON.stringify(state.news))
        },
        SET_COURSES: (state, courses) => {
            state.courses = courses;
            localStorage.setItem('courses', JSON.stringify(state.courses))
        },
        SET_PRODUCT(state, product) {
            state.product = product
            localStorage.setItem('product', JSON.stringify(state.product))
        },
        SET_NEWSERV(state, newservices) {
            state.newservices = newservices
            // localStorage.setItem('newservices', JSON.stringify(state.newservices)
        },
        SET_SERVICES(state, services) {
            state.services = services
            localStorage.setItem('services', JSON.stringify(state.services))
        },
        SET_TRAINING(state, training) {
            state.training = training
            localStorage.setItem('training', JSON.stringify(state.training))
        },
        findById: (state, _id) => {
            state.employee._id = _id;
        }
    },
    actions: {
        GET_EMPLOYEE({
            commit
        }) {
            return axios('http://31.31.203.119/api/employees', {
                method: "GET"
            })
                .then((employee) => {
                    commit('SET_EMPLOYEE', employee.data)
                    // ะัะทัะฒะฐะตะผ ะผััะฐัะธั ะธ ะฟะตัะตะดะฐะตะผ employee
                    return employee;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_CATEGORY({
            commit,
        }) {
            return axios(`http://31.31.203.119/api/categories`, {
                method: "GET"
            })
                .then((category) => {
                    commit('SET_CATEGORY', category.data)
                    // ะัะทัะฒะฐะตะผ ะผััะฐัะธั ะธ ะฟะตัะตะดะฐะตะผ employee
                    return category;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_PRODUCT({
            commit
        }, product) {

            commit('SET_PRODUCT', product)
        },
        GET_NEWSERV({
            commit
        }, newservices) {

            commit('SET_NEWSERV', newservices)
        },
        GET_NEWS({
            commit
        }) {
            return axios('http://31.31.203.119/api/news', {
                method: "GET"
            })
                .then((news) => {
                    commit('SET_NEWS', news.data)
                    return news;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        // GET_COURSES({
        //     commit
        // }) {
        //     return axios('https://efd36b5b3897.ngrok.io/training', {
        //         method: "GET"
        //     })
        //         .then((courses) => {
        //             commit('SET_COURSES', courses.data)
        //             return courses;
        //         })
        //         .catch((error) => {
        //             console.log(error)
        //             return error;
        //         })
        // },
        GET_SERVICES({
            commit
        }) {
            return axios('http://31.31.203.119/api/courses', {
                method: "GET"
            })
                .then((services) => {
                    commit('SET_SERVICES', services.data)
                    return services;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        GET_TRAINING({
            commit
        }) {
            return axios('http://31.31.203.119/api/trainings', {
                method: "GET"
            })
                .then((training) => {
                    commit('SET_TRAINING', training.data)

                    return training;
                })
                .catch((error) => {
                    console.log(error)
                    return error;
                })
        },
        findById({
            commit
        }, _id) {
            commit('findById', _id)
        }
    },
    getters: {
        EMPLOYEE(state) {
            return state.employee;
        },
        CATEGORY(state) {
            return state.category;
        },
        NEWS(state) {
            return state.news;
        },
        COURSES(state) {
            return state.courses;
        },
        TRAINING(state) {
            return state.training;
        },
        COSMECEUTICALS(state) {
            return state.cosmeceuticals;
        },
        PRODUCT(state) {
            return state.product;
        },
        SERVICES(state) {
            return state.services;
        },
        NEWSERVICES(state) {
            return state.newservices;
        },
        CourseById: state => name => {
            return state.courses.find(todo => todo.name === name)
        },
        NewsById: state => name => {
            return state.news.find(todo => todo.name === name)
        },
        findByCateg: s => path => s.category.find(t => t.path === path),
        findById: s => _id => s.employee.find(t => t._id === _id),
        //ะะฐัะตะณะพัะธะธ/:article
        ProductById: s => article => s.product.find(t => t.article === article),
        ConsumablesById: s => article => s.consumables.find(t => t.article === article),
        MesoById: s => article => s.meso.find(t => t.article === article),
        PlasticById: s => article => s.plastic.find(t => t.article === article),
        PdoById: s => article => s.pdo.find(t => t.article === article),
        OtherById: s => article => s.other.find(t => t.article === article)

    }
})
export default store;