<template>
  <!-- <a href="#header" -->
  <div class="services__item" @click="find(newserv), scrollToEnd()">
    {{ newserv.service }}
  </div>
</template>

<script>
export default {
  name: "services-item",
  props: {
    newserv: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    find(newserv) {
      this.$emit("find", this.newserv);
    },
    scrollToEnd: function () {
      var container = this.$el.querySelector("#header");
      if (window.innerWidth < 1300) {
        let slide = document.getElementById("serv");

        let top = window.scrollY + slide.getBoundingClientRect().y;

        window.scrollTo({
          top,
          behavior: "smooth",
        });
      } else {
        window.scrollTo({
          top: 270,
          behavior: "smooth",
        });
      }
    },
  },
  mounted() {
    this.$emit("find", this.newserv);
  },
};
</script>

