<template>
  <figure class="employee-item" @click="find()">
    <img
      :src="require('../assets/img/empl/' + employee_data.article + '.jpg')"
      alt="img"
      width="250"
      height="250"
    />
    <figcaption class="employee-item__fig">
      <p class="employee-item__name">{{ employee_data.name }}</p>
      <p class="employee-item__surname">{{ employee_data.surname }}</p>
      <p class="employee-item__p" :class="classes">
        {{ employee_data.description }}
      </p>
      <p
        v-if="isActive && employee_data.description.length >= 30"
        @click="met()"
        class="scroll__p"
      >
        ะงะธัะฐัั ะดะฐะปะตะต
      </p>
      <p v-if="!isActive" @click="met()" class="scroll__p">ะกะบัััั</p>
    </figcaption>
  </figure>
</template>
<script>
export default {
  name: "employee-item",
  data() {
    return {
      isActive: true,
    };
  },
  props: {
    employee_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    find() {
      this.$emit("find", this.employee_data);
    },
    met() {
      this.isActive = !this.isActive;
      console.log(this.employee_data.description.length);
    },
    scrollToEnd: function () {
      let slide = document.getElementById("empl-block");

      let top = window.scrollY + slide.getBoundingClientRect().y;
      console.log(top);
      window.scrollTo({
        top,
        behavior: "smooth",
      });
    },
  },

  computed: {
    classes: function () {
      return {
        scroll: this.isActive,
        // btnotactive: !this.isActive,
        // btnactive: this.isActive && !this.notActive,
        // btnotactive: !this.isActive && this.notActive,
      };
    },
  },
  mounted: function () {
    this.$emit("find", this.employee_data);
  },
};
</script>