<template>
  <div class="employee">
    <div class="employee__block" id="empl-block">
      <EmployeeItem
        @find="find"
        v-for="employee in EMPLOYEE"
        :key="employee.article"
        v-bind:employee_data="employee"
      />
    </div>
    <!-- <div class="employee__block2" id="empl">
      <Emp v-bind:staff="staff" />
    </div> -->
  </div>
</template>
<script>
import EmployeeItem from "@/components/employee-item";
import Emp from "@/components/emp";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "employee",
  data() {
    return {
      staff: [],
    };
  },
  components: {
    EmployeeItem,
    Emp,
  },
  computed: {
    ...mapGetters(["EMPLOYEE"]),
  },
  methods: {
    ...mapActions(["GET_EMPLOYEE"]),
    find(employee_data) {
      this.staff.splice(employee_data, 1);
      this.staff.push({
        _id: employee_data._id,
        article: employee_data.article,
        image: employee_data.image,
        name: employee_data.name,
        surname: employee_data.surname,
        patronymic: employee_data.patronymic,
        position: employee_data.position,
        description: employee_data.description,
      });
    },
  },
  mounted() {
    this.GET_EMPLOYEE()
      .then((response) => {
        if (response.data) {
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>