import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/index.vue'
import News from '../views/news.vue'
import Company from '../views/company.vue'
import Staff from '../views/staff.vue'
import Categories from '../views/categories.vue'
import Contact from '../views/contact.vue'
import Services from '../views/ services.vue'
import Errors from '../views/error.vue'
import Learn from '../views/program-training.vue'




Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'index',
    component: Index

},
{
    path: '/news',
    name: 'news',
    component: News
},
{
    path: '/company',
    name: 'company',
    component: Company
},
{
    path: '/staff',
    name: 'staff',
    component: Staff
},
{
    path: '/categories',
    name: 'categories',
    component: Categories

},
{
    path: '/contact',
    name: 'contact',
    component: Contact,
},

{
    path: '/categories/:path',
    name: 'cosmeceuticals',
    component: () =>
        import('../views/cosmeceuticals.vue')

},
{
    path: '/categories/:path/:article',
    name: 'cosmeceuticalsArticle',
    component: () =>
        import('../views/cosmeceuticals-product.vue')

},
//ะัะพะณัะฐะผะผั ะพะฑััะตะฝะธั
{
    path: '/training',
    name: 'training',
    component: Learn
},
//ะกะตะผะธะฝะฐัั
{
    path: '/courses',
    name: 'courses',
    component: () =>
        import('../views/training.vue')

},
{
    path: '/training/:name',
    name: 'trainingid',
    component: () =>
        import('../views/more-courses.vue')

}, {
    path: '/news/:name',
    name: 'newsid',
    component: () =>
        import('../views/more-news.vue')

}, {
    path: '/documents',
    name: 'documents',
    component: () =>
        import('../views/info-legal.vue')

}, {
    path: '/services',
    name: 'services',
    component: Services
},

{
    path: "/*",
    component: Errors
    // component: { render: (h) => h("div", ["404! Page Not Found!"]) },
},
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior(to, from, savedPosition) {
        return { x: 0, y: 0 }
    },
})

export default router