<template>
  <div class="services__wrap" id="servicesbcg" :class="classes" @click="met">
    <p
      class="services__li-index"
      id="servicescolor"
      @click="categ(category_data), scrollToEnd()"
    >
      {{ category_data.name }}
    </p>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "categ-index",
  data() {
    return {
      newservices: [],
      isActive: false,
    };
  },
  props: {
    category_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    classes: function () {
      return {
        btnactive: this.isActive,
        // btnotactive: !this.isActive,
        // btnactive: this.isActive && !this.notActive,
        // btnotactive: !this.isActive && this.notActive,
      };

      // (c = this.classes), (c.btnactive = false), (c.btnotactive = true);
    },
  },
  methods: {
    categ(category_data) {
      this.newservices.splice(category_data, 1);
      axios
        .get(`http://31.31.203.119/api/services/${category_data.path}`, {})
        .then((response) => {
          this.newservices = response.data;
          this.$store.dispatch("GET_NEWSERV", this.newservices);
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },
    met() {
      let parent = document.querySelector("#parent");
      this.isActive = !this.isActive;
      let nodes = parent.childNodes;
      // nodes.classList.add("btnactive");

      for (let node of nodes) {
        if (this.isActive) {
          node.classList.remove("btnactive");
        }
      }
    },
    scrollToEnd: function () {
      var container = this.$el.querySelector("#header");
      if (window.innerWidth < 800) {
        let slide = document.getElementById("serv_column");

        let top = window.scrollY + slide.getBoundingClientRect().y;

        window.scrollTo({
          top,
          behavior: "smooth",
        });
      }
    },
  },
  mounted() {
    axios
      .get(`http://31.31.203.119/api/services/contour_plastic`, {})
      .then((response) => {
        this.newservices = response.data;
        this.$store.dispatch("GET_NEWSERV", this.newservices);
      })
      .catch((error) => {
        console.log(error);
        return error;
      });
    let parent = document.querySelector("#parent");
    // parent.firstChild.style.background = "white";
    // parent.firstChild.style.color = "black";
    parent.firstChild.classList.add("btnactive");

    // const paretn = document.getElementById("parent");
    // document.getElementById("servicesbcg").style.background = "white";
    // document.getElementById("servicescolor").style.color = "black";
    // }
  },
};
</script>

